.Counter{
  background-image: url(./img/BACKGROUND.jpg);
  width: 400px;
  height: 400px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
  position: relative;
  left: 600px;
  top: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family:  sans-serif;
}
button{
  width: 30px;
  height: 30px;
  background-color: rgb(183, 190, 184);
  border: 0px;
  border-radius: 50%;
  
}
.btn{
  background-color: rgba(143, 141, 141, 0.484);
}
.btn1{
  width: 150px;
  height: 40px;
  border-radius: 10px;
  margin-top: 20px;
  font-weight: 700;
  background-color:burlywood;
}
.count{
  backdrop-filter: blur(100px);
  width: 200px;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
}